<template>
	<div id="gradeFee">
		<span class="title">{{$t('GoldWithdrawalFee')}}</span>
		<el-col :span="24" style="padding-bottom: 0px" class="toolbar">
 		        <el-form :inline="true" :model="goldForm"  label-position="left">
					<el-col :span = "24" style="padding-bottom: 0px" class="toolbar">
						<el-form-item :label="'100g'+$t('GoldWithdrawalFee')" >
					    	<el-input v-model="goldForm.gFee" :placeholder="$t('PleaseService')"></el-input>
						</el-form-item>
						<el-form-item>
					    <el-button type="warning"@click="setGoldDrawFee(1,goldForm.gFee)">{{$t('confirm')}}</el-button>
						</el-form-item>
					</el-col>
					<el-col :span = "24" style="padding-bottom: 0px" class="toolbar">
						<el-form-item :label="'1000g'+$t('GoldWithdrawalFee')" >
					    	<el-input v-model="goldForm.kgFee" :placeholder="$t('PleaseService')"></el-input>
						</el-form-item>
						<el-form-item>
					    <el-button type="warning"@click="setGoldDrawFee(2,goldForm.kgFee)">{{$t('confirm')}}</el-button>
						</el-form-item>
					</el-col>
 		        </el-form>
	      </el-col> 	
	</div>
</template>
<script>
import {ajax} from '../../common/js/common.js'

	export default{
		data(){
			return {
				goldForm:{
					gFee:"",
					kgFee:""
				}
			}
		
		},
		methods:{
			showGoldDrawFee:function(){
				ajax(this,this.ApiConfig.feeConfigure.showGoldDrawFee,{},(res) =>{
					console.log(res)
					if(res.code == 200){
						this.goldForm.gFee = res.data.G100g;
						this.goldForm.kgFee = res.data.G1000g;
					}else{
						this.$notify({
							title:"提示",
							message:res.message,
							type:'warning'
						})
						return false;
					}
				},"POST")
			},
			setGoldDrawFee:function(type,fee){
				let params = {
					type:type,
					fee:fee
				};
				ajax(this,this.ApiConfig.feeConfigure.setGoldDrawFee,params,(res) =>{
					console.log(res)
					if(res.code == 200){
						this.$notify({
							title:"提示",
							message:res.message,
							type:'success'
						})
						return false;
					}else{
						this.$notify({
							title:"提示",
							message:res.message,
							type:'warning'
						})
						return false;

					}
				},"POST")

			}

		},
		mounted(){
			this.showGoldDrawFee();
		}
	}
</script>
<style>
	#gradeFee{
		
		.el-button{
			/* width: 120px;
			height: 40px; */
			background-color: #bf9764;
			/* font-size: 16px; 
			border-radius: 6px;
			border: none;
			outline: none; */
		}
		.el-form-item__label{
			font-size: 14px;
    		color: #000;
			width:168px;
		}

		.el-input__inner{
			font-size: 14px;
			}
}
</style>


